:root {
  --WHITE: #fff;
  --LIGHT-BKG: #fafafa;
  --DARK-BKG: #1e1823;
  --ACCENT: #fd9222;
  --ACCENT-LIGH: #fcf1e0;
  --DARK: #111;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--LIGHT-BKG);
  color: var(--DARK);
  letter-spacing: -.28px;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}

.list {
  list-style-type: none;
  display: flex;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.clear-style {
  appearance: none;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.link {
  cursor: pointer;
  text-decoration: none;
}

.accent {
  color: var(--ACCENT);
}

.section-title {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: -.6px;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
  display: block;
}

@media screen and (min-width: 768px) {
  .section-title {
    letter-spacing: -.72px;
    font-size: 24px;
    font-weight: 600;
    line-height: 1.17;
  }
}

.btn {
  cursor: pointer;
  outline: none;
  display: block;
}

.btn-45 {
  height: 45px;
  border-radius: 100px;
  padding: 0 28px;
}

.btn-41 {
  height: 41px;
  border-radius: 100px;
  padding: 0 28px;
}

.btn-33 {
  height: 33px;
  border-radius: 100px;
  padding: 0 20px;
}

.btn-29 {
  height: 29px;
  border-radius: 100px;
  padding: 0 16px;
}

.btn-24 {
  height: 24px;
  border-radius: 35px;
  padding: 0 16px;
}

.btn-accent {
  background-color: var(--ACCENT);
  border: 1px solid var(--ACCENT);
  color: var(--WHITE);
}

.btn-accent:hover, .btn-accent:focus {
  background-color: var(--ACCENT-LIGH);
  border: 1px solid var(--ACCENT-LIGH);
  color: var(--ACCENT);
}

.btn-outln {
  border: 1px solid var(--WHITE);
  color: var(--WHITE);
  background-color: #0000;
}

.btn-outln:hover, .btn-outln:focus {
  border: 1px solid var(--ACCENT);
  color: var(--ACCENT);
  background-color: #0000;
}

.btn-outln-accent {
  border: 1px solid var(--ACCENT);
  color: var(--DARK);
  background-color: #0000;
}

.btn-outln-accent:hover, .btn-outln-accent:focus {
  background-color: var(--ACCENT);
  border: 1px solid var(--ACCENT);
  color: var(--WHITE);
}

.btn-other {
  background-color: var(--LIGHT-BKG);
  color: var(--DARK);
  border: 1px solid #1111111a;
}

.modal {
  width: 100%;
  height: 100%;
  background-color: #2e2f4266;
  position: fixed;
  top: 0;
  left: 0;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-nav-container {
  align-items: center;
  display: flex;
}

.header-logo {
  color: var(--DARK);
  letter-spacing: -.42px;
  text-transform: uppercase;
  align-items: center;
  gap: 4px;
  font-style: italic;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

.header-modal-button {
  background-color: #0000;
  border: none;
  position: absolute;
  top: 20px;
  right: 20px;
}

.header-nav-container-mobile {
  margin-top: 136px;
  margin-bottom: auto;
  margin-left: 50px;
}

.header-nav-links-mobile {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.header-nav-links-mobile .link {
  color: var(--WHITE);
  font-weight: 500;
  line-height: 1.14;
}

.header-nav-links-mobile .link:focus {
  color: var(--ACCENT);
}

.header-social-media-svg-mobile {
  fill: var(--WHITE);
}

.header-social-media-svg-mobile:focus {
  fill: var(--ACCENT);
}

.header-social-media-links, .header-social-media-links-mobile {
  gap: 18px;
  display: flex;
}

.js-open-menu {
  background-color: #0000;
  border: none;
}

.js-menu-container {
  background-color: var(--DARK-BKG);
  z-index: 999;
  width: 100vw;
  height: 100vh;
  flex-direction: column;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.js-menu-container.is-open {
  transform: translateX(0);
}

@media screen and (max-width: 767px) {
  .header-container {
    padding-top: 20px;
    padding-bottom: 14px;
  }

  .header-social-media-container-mobile {
    margin-bottom: 20px;
    margin-left: 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .header-container {
    padding-top: 24px;
    padding-bottom: 24px;
  }

  .header-hamburger {
    width: 32px;
    height: 32px;
  }

  .header-modal-button {
    top: 24px;
    right: 32px;
  }

  .header-social-media-container-mobile {
    margin-bottom: 24px;
    margin-left: 32px;
  }

  .header-social-media-svg-mobile {
    width: 24px;
    height: 24px;
  }
}

@media screen and (max-width: 1199px) {
  .header-nav-links, .header-social-media-container {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .js-open-menu, .header-mobile-menu {
    display: none;
  }

  .header-container {
    padding-top: 24px;
    padding-bottom: 32px;
  }

  .header-logo {
    letter-spacing: -.54px;
    margin-right: 188px;
    font-size: 18px;
    line-height: 1.11;
  }

  .header-logo-svg {
    width: 24px;
    height: 24px;
  }

  .header-nav-links {
    gap: 24px;
    display: flex;
  }

  .header-nav-links .link {
    color: #111;
    letter-spacing: -.28px;
    font-weight: 500;
    line-height: 1.14;
  }

  .header-nav-links .link:hover, .header-nav-links .link:focus {
    color: var(--ACCENT);
  }

  .social-media-icon {
    transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  }

  .header-social-media-svg:hover, .header-social-media-svg:focus {
    fill: var(--ACCENT);
  }
}

